module.exports = " <div id=quiz-paper> <div class=flex-column v-show=modalShow> question analysis model </div> <div class=\"wrapper flex-column\"> <div class=flex-row> <mdl-button class=icon-left-button raised primary @click=$goBack()><i class=material-icons>keyboard_arrow_left</i>返回</mdl-button> <mdl-button class=icon-left-button raised accent v-show=\"!sampleMode || observeMode\" @click=showAnswers() style=margin-left:16px> <i class=material-icons style=\"margin-right: 8px\">visibility</i> <span>顯示 / 隱藏答案</span> </mdl-button> </div> <div v-if=observeMode class=\"obserMode_flag flex-column flex-center\"> <span style=font-size:18px;color:#FFFFFF class=\"flex-row flex-center\"> <i class=material-icons>visibility</i> <span>&nbsp;觀察模式</span> </span> </div> <div class=second-wrapper> <div class=third-wrapper> <card v-if=\"sampleMode && !quickquiz.finished\"> <div slot=content class=flex-column style=\"padding:16px;text-align:center;font-size: 16px;color: #FF9800\"> 測驗尚未結束，未統計數據 </div> </card> <card> <div slot=content class=flex-column style=padding:16px v-if=quickquiz.title> <span class=\"flex-row flex-center\"> <span v-show=sampleMode style=\"font-size:16px;padding: 4px 8px;margin-right:8px;background:#2196F3;color:#fff\">答卷</span> <span class=title>{{quickquiz.title}}</span> <span class=\"time flex-row\" style=margin-left:auto><i class=material-icons>timer</i>{{quickquiz.time}}分鐘</span> </span> <span class=author>出卷人: {{quickquiz.createdBy.name}}</span> <div v-if=\"sampleMode && quizsample.student\" style=\"border-top:1px solid #ddd;margin-top:16px;padding-top: 16px\"> <span class=\"flex-row flex-center\"> <span class=flex-column style=\"margin-right: 16px\"> <span class=field-title>姓名</span> <span class=field-content>{{quizsample.student.name}}</span> </span> <span class=flex-column style=\"margin-right: 32px\"> <span class=field-title>學號</span> <span class=field-content>{{quizsample.student.schoolId}}</span> </span> <span class=flex-row style=\"margin-left: auto\" v