tip> </div> </div> </card> <div id=question-body> <card v-for=\"question in quickquiz.questions\" track-by=_id class=question :id=\"'question_' + $index\"> <div v-if=\"typeof question === 'object'\" slot=content class=flex-column> <div class=\"overlay flex-column\" :class=\"{'overlay_show': overlay.on === $index}\"> <div class=flex-row style=\"justify-content: flex-end\"> <mdl-button icon @click=hideOverlay()> <i class=material-icons>close</i> </mdl-button> </div> <div v-if=\"overlay.type === 0\"> <div class=flex-row> <div class=\"flex-50 flex-column\" style=\"padding: 0 16px 16px 16px\"> <span class=rightPeople_header>正確 {{accuracy[$index]}}</span> <p>{{question.rightPeople}}</p> </div> <div class=\"flex-50 flex-column\" style=\"padding: 0 16px 16px 16px\"> <span class=wrongPeople_header>錯誤</span> <p>{{question.wrongPeople}}</p> </div> </div> </div> <div v-else> <canvas :id=\"'qaChart_' + $index\"></canvas> </div> </div> <span class=\"flex-row flex-center\" style=padding:16px> <div class=\"flex-column flex-center\"> <span class=index-label>{{$index + 1}}</span> <div class=\"flex-column flex-center\" v-if=quickquiz.finished> <span class=accuracy :id=\"'analysis_' + $index\" @click=\"showOverlay($index, 1)\">{{accuracy[$index]}}</span> <mdl-tooltip :for=\"'analysis_' + $index\" large class=flex-column> <div class=flex-row style=margin-bottom:8px> <span style=margin-right:4px>正確: {{quickquiz.analysis.questions[$index][0]}}</span> <span>錯誤: {{quickquiz.analysis.questions[$index][1]}}</span> </div> <div class=flex-row> <span style=margin-right:4px>留空: {{quickquiz.analysis.questions[$index][2]}}</span> <span>例外: {{quickquiz.analysis.questions[$index][3]}}</span> </div> </mdl-tooltip> </div> </div> <span class=question_body v-html=question.context></span> <div class=flex-row style=\"margin-left:auto;flex-shrink: 0\" v-if=quickquiz.finished> <mdl-button accent @click=showAnswer($index) v-show=\"quickquiz.correctAnswers[$index] !== null && !sampleMode\"> <span v-if=\"typeof